<template>
  <div class="acceidit">
    <Card>
      <div slot="title">密码修改</div>
      <div slot="content">
        <el-form
          :model="accEiditForm"
          :rules="rules"
          ref="accEiditForm"
          label-width="100px"
          class="accEidit-form"
        >
          <!-- 原密码 -->
          <el-form-item label="原密码" prop="pass">
            <el-input v-model="accEiditForm.pass" autocomplete="off"></el-input>
          </el-form-item>
          <!-- 密码 -->
          <el-form-item label="新密码" prop="newpass">
            <el-input
              type="password"
              v-model="accEiditForm.newpass"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <!-- 权限 -->
          <el-form-item label="确认新密码" prop="checkPass">
            <el-input
              type="password"
              v-model="accEiditForm.checkPass"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <!-- 修改 -->
          <el-form-item>
            <el-button type="primary" @click="accEiditBtn">修改</el-button>
            <el-button @click="resetFormBtn(accEiditForm)">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </Card>
  </div>
</template>

<script>
//引入卡片插槽
import Card from "@/components/Card";
//密码验证规则
import { PWD } from "@/units/regular.js";
//引入相关api
import { api_eiditOldPwd, api_accNewPwd } from "@/api/acc.js";
export default {
  components: {
    Card,
  },
  data() {
    //原密码验证
    const oldPwd = async (rules, value, callback) => {
      if (!value) {
        callback(new Error("原密码不能为空"));
      } else {
        let { code, msg } = await api_eiditOldPwd({
          oldPwd: value,
        });
        if (code == "11") {
          callback(new Error(msg));
        }
      }
    };
    //设置新密码验证
    const newPwd = (rules, value, callback) => {
      if (!value) {
        callback(new Error("新密码不能为空"));
      } else if (!PWD.test(value)) {
        callback(new Error("请输入6-8位字母或数字"));
      } else {
        callback();
      }
    };
    //确认密码验证
    const checkNewPwd = (rules, value, callback) => {
      if (!value) {
        callback(new Error("确认密码不能为空"));
      } else if (value !== this.accEiditForm.newpass) {
        callback(new Error("两次输入的密码不同，请重新输入！"));
      } else {
        callback();
      }
    };

    return {
      //整个表单的双向绑定的数据
      accEiditForm: {
        checkPass: "",
        pass: "",
        newpass: "",
      },
      rules: {
        pass: { validator: oldPwd, trigger: "blur" },
        newpass: { validator: newPwd, trigger: "blur" },
        checkPass: { validator: checkNewPwd, trigger: "blur" },
      },
    };
  },
  methods: {
    accEiditBtn() {
      this.$refs.accEiditForm.validate(async (valid) => {
        if (valid) {
          let { code, msg } = await api_accNewPwd({
            newPwd: this.accEiditForm.newpass,
          });
          if (code == 0) {
            this.$router.push("/login");
          }
        } else {
          this.$message.error(msg);
          return false;
        }
      });
    },
    resetFormBtn(accEiditForm) {
      this.$refs.accEiditForm.resetFields();
    },
  },
};
</script>

<style lang="less" scoped>
.acceidit {
  background: #fff;
  user-select: none;
  border-radius: 4px;
  overflow: hidden;

  .el-form {
    padding: 10px;
    .el-input {
      width: 40%;
    }
  }
}
</style>